import React from "react";
import NavBarItem from "./NavBarItem/NavBarItem";
import TitleNav from "./TitleNav/TitleNav";
import "./NavBar.css";

const navbarItems = [
  {
    type: "Stays",
    icon: "fa-bed",
    active: true,
  },
  {
    type: "Flights",
    icon: "fa-plane",
    active: false,
  },
  {
    type: "Car rentals",
    icon: "fa-car",
    active: false,
  },
  {
    type: "Attractions",
    icon: "fa-bed",
    active: false,
  },
  {
    type: "Airport taxis",
    icon: "fa-taxi",
    active: false,
  },
];

class Navbar extends React.Component {
  render() {
    return (
      <div >
        <nav className="px-5 navbar-expand-sm">
          <div className="">
            <TitleNav />
            <div className="">
              <ul className="d-flex justify-content-start">
                {navbarItems.map((item, index) => (
                  <NavBarItem item={item} key={index} />
                ))}
              </ul>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default Navbar;
